<template>
  <div class="file-link-detector">
    <!-- ‰ΩøÁî®Âä®ÊÄÅÂºïÁî®Â§ÑÁêÜÊñáÊú¨ÁÇπÂáª -->
    <div ref="contentContainer" @click="handleTextClick"></div>
    
    <!-- ÊòæÁ§∫ÊâæÂà∞ÁöÑÊñá‰ª∂ÈìæÊé• -->
    <div v-if="detectedFiles.length > 0" class="detected-files">
      <div class="files-section-title">Áõ∏ÂÖ≥Êñá‰ª∂:</div>
      <div class="file-links">
        <div 
          v-for="(file, index) in detectedFiles" 
          :key="index"
          class="file-link"
          @click="openFile(file)"
        >
          <span class="file-icon">{{ getFileIcon(file.fileType) }}</span>
          <span class="file-name">{{ file.fileName }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { defineComponent, ref, onMounted, watch } from 'vue';

export default defineComponent({
  name: 'FileLinkDetector',
  props: {
    content: {
      type: String,
      required: true
    }
  },
  emits: ['file-preview'],
  setup(props, { emit }) {
    const detectedFiles = ref([]);
    const contentContainer = ref(null);
    
    // Êñá‰ª∂Á±ªÂûãÂõæÊ†áÊò†Â∞Ñ
    const getFileIcon = (type) => {
      const icons = {
        'markdown': 'üìù',
        'report': 'üìä',
        'html': 'üåê',
        'image': 'üñºÔ∏è',
        'json': 'üìã',
        'csv': 'üìà',
        'other': 'üìÑ'
      };
      return icons[type] || icons.other;
    };
    
    // Êõ¥Êñ∞ÂÜÖÂÆπÊòæÁ§∫
    const updateContent = () => {
      if (!contentContainer.value) return;
      
      let text = props.content;
      
      // Ê£ÄÊü•ÊñáÊú¨‰∏≠ÊòØÂê¶Â∑≤ÂåÖÂê´HTMLÊ†áÁ≠æ
      const containsHtmlTags = /<[^>]+>/.test(text);
      if (containsHtmlTags) {
        contentContainer.value.innerHTML = text;
        return;
      }
      
      // ËΩ¨‰πâHTMLÁâπÊÆäÂ≠óÁ¨¶
      text = text.replace(/</g, '&lt;').replace(/>/g, '&gt;');
      
      // Êñá‰ª∂Ë∑ØÂæÑÈ´ò‰∫ÆÂ§ÑÁêÜ
      detectedFiles.value.forEach(file => {
        // ‰∏∫Êñá‰ª∂Ë∑ØÂæÑÊ∑ªÂä†È´ò‰∫ÆÊ†∑Âºè
        const escapedPath = file.originalMatch.replace(/[.*+?^${}()|[\]\\]/g, '\\$&');
        const regex = new RegExp(escapedPath, 'g');
        text = text.replace(regex, `<span class="file-path-highlight" data-file-index="${detectedFiles.value.indexOf(file)}">${file.originalMatch}</span>`);
      });
      
      contentContainer.value.innerHTML = text;
    };
    
    // ÊñáÊú¨ÁÇπÂáªÂ§ÑÁêÜ
    const handleTextClick = (event) => {
      // Ê£ÄÊü•ÊòØÂê¶ÁÇπÂáª‰∫ÜÊñá‰ª∂Ë∑ØÂæÑÈ´ò‰∫ÆÂå∫Âüü
      if (event.target.classList.contains('file-path-highlight')) {
        const fileIndex = parseInt(event.target.dataset.fileIndex);
        if (!isNaN(fileIndex) && fileIndex >= 0 && fileIndex < detectedFiles.value.length) {
          openFile(detectedFiles.value[fileIndex]);
        }
      }
    };
    
    // Ê£ÄÊµãÊñáÊú¨‰∏≠ÁöÑÊñá‰ª∂Ë∑ØÂæÑ
    const detectFilePaths = async () => {
      // Ê∏ÖÁ©∫ÊóßÁöÑÊ£ÄÊµãÁªìÊûú
      detectedFiles.value = [];
      
      const content = props.content;
      
      // Êõ¥Á≤æÁ°ÆÁöÑÊñá‰ª∂Ê®°ÂºèÂåπÈÖç - Âè™ÊúâÁâπÂÆöÂâçÁºÄÊâçËØÜÂà´Êñá‰ª∂
      const filePatterns = [
        {
          // ÂåπÈÖçstoryteller/outputË∑ØÂæÑ
          pattern: /storyteller\/output(?:\/[\w.-]+)*\/([^/\s,Ôºå.„ÄÇ]+\.\w+)/g,
          type: 'auto'
        },
        {
          // ÂåπÈÖçiterationsË∑ØÂæÑ
          pattern: /iterations\/([^/\s,Ôºå.„ÄÇ]+\.\w+)/g,
          type: 'auto'
        },
        {
          // ÂåπÈÖç"Â∑≤‰øùÂ≠òÂà∞"ÂêéÈù¢ÁöÑÊñá‰ª∂Ë∑ØÂæÑ
          pattern: /Â∑≤‰øùÂ≠òÂà∞\s*(?:\/[\w.-]+)*\/([^/\s,Ôºå.„ÄÇ]+\.\w+)/g,
          type: 'auto'
        },
        {
          // ÂåπÈÖç"generated:"ÂêéÈù¢ÁöÑÊñá‰ª∂
          pattern: /generated:\s*([a-zA-Z0-9_-]+\.\w+)/gi,
          type: 'auto'
        }
      ];
      
      const foundFiles = [];
      
      // ÈÄêË°åÊ£ÄÊü•Êñá‰ª∂ÂºïÁî®
      const lines = content.split('\n');
      lines.forEach(line => {
        filePatterns.forEach(({ pattern, type }) => {
          let match;
          while ((match = pattern.exec(line)) !== null) {
            if (match[1]) {
              const fileName = match[1].trim().replace(/[,Ôºå.„ÄÇ"']/g, '');
              const basePath = '/storyteller/output/';
              const filePath = `${basePath}${fileName}`;
              
              // Á°ÆÂÆöÊñá‰ª∂Á±ªÂûã
              let fileType = type;
              if (type === 'auto') {
                const ext = fileName.split('.').pop().toLowerCase();
                switch (ext) {
                  case 'md': fileType = 'markdown'; break;
                  case 'html': fileType = 'html'; break;
                  case 'png': case 'jpg': case 'jpeg': case 'gif': case 'svg': fileType = 'image'; break;
                  case 'json': fileType = 'json'; break;
                  case 'csv': fileType = 'csv'; break;
                  default: fileType = 'other';
                }
              }
              
              // Ê∑ªÂä†Âà∞ÊâæÂà∞ÁöÑÊñá‰ª∂ÂàóË°®
              if (!foundFiles.some(f => f.fileName === fileName)) {
                foundFiles.push({
                  fileName,
                  filePath,
                  fileType,
                  originalMatch: match[0]
                });
              }
            }
          }
          // ÈáçÁΩÆÊ≠£ÂàôË°®ËææÂºèÁä∂ÊÄÅÔºåÈÅøÂÖç‰∏ãÊ¨°Âæ™ÁéØÈóÆÈ¢ò
          pattern.lastIndex = 0;
        });
      });
      
      // Ê£ÄÊü•Êñá‰ª∂ÊòØÂê¶Â≠òÂú®
      for (const file of foundFiles) {
        try {
          const response = await fetch(file.filePath, { method: 'HEAD' });
          if (response.ok) {
            detectedFiles.value.push(file);
            console.log(`Á°ÆËÆ§Êñá‰ª∂Â≠òÂú®: ${file.filePath}`);
          }
        } catch (err) {
          console.error(`Ê£ÄÊü•Êñá‰ª∂Â≠òÂú®ÊÄßÂ§±Ë¥•: ${file.filePath}`, err);
        }
      }
      
      // Ê£ÄÊµãÂÆåÊàêÂêéÊõ¥Êñ∞ÂÜÖÂÆπÊòæÁ§∫
      updateContent();
    };
    
    // ÊâìÂºÄÊñá‰ª∂È¢ÑËßà
    const openFile = (file) => {
      emit('file-preview', file);
    };
    
    // ËßÇÂØüÂÜÖÂÆπÂèòÂåñ
    watch(() => props.content, () => {
      detectFilePaths();
    });
    
    // ÁªÑ‰ª∂ÊåÇËΩΩÊó∂Ê£ÄÊµãÊñá‰ª∂Ë∑ØÂæÑ
    onMounted(() => {
      detectFilePaths();
    });
    
    // Ê£ÄÊµãÂÆåÊàêÂêéÊõ¥Êñ∞ÂÜÖÂÆπ
    watch(detectedFiles, () => {
      updateContent();
    });
    
    return {
      contentContainer,
      detectedFiles,
      getFileIcon,
      openFile,
      handleTextClick
    };
  }
});
</script>

<style scoped>
.file-link-detector {
  position: relative;
}

.detected-files {
  margin-top: 12px;
  border-top: 1px solid #eee;
  padding-top: 8px;
}

.files-section-title {
  font-size: 13px;
  color: #888;
  margin-bottom: 8px;
}

.file-links {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.file-link {
  display: flex;
  align-items: center;
  background-color: #f5f5f5;
  border: 1px solid #e0e0e0;
  border-radius: 4px;
  padding: 4px 8px;
  font-size: 13px;
  cursor: pointer;
  transition: all 0.2s;
}

.file-link:hover {
  background-color: #e8e8e8;
  border-color: #d0d0d0;
}

.file-icon {
  margin-right: 6px;
}

.file-name {
  color: #1890ff;
}

:deep(.file-path-highlight) {
  color: #1890ff;
  background-color: rgba(24, 144, 255, 0.1);
  padding: 2px 4px;
  border-radius: 3px;
  cursor: pointer;
}
</style> 