<template>
    <div class="main-container">
      <div class="chat-container">
        
        <div class="chat-messages">
          <!-- User query bubble (right side) -->
          <div class="message-group user-message">
            <div class="message-content user-content">
              <div class="message-bubble user-bubble">
                <p>{{ query }}</p>
              </div>
              <div class="attachment-section" @click="toggleDatasetPreview">
                <div class="attachment-card">
                  <div class="attachment-icon">üìä</div>
                  <div class="attachment-info">
                    <div class="attachment-name">shopping.csv</div>
                    <div class="attachment-meta">Dataset ‚Ä¢ Click to view</div>
                  </div>
                </div>
              </div>
            </div>
            <div class="avatar user-avatar">
              <span class="avatar-icon">üë§</span>
            </div>
          </div>
          
          <!-- AI response bubble (left side) -->
          <div class="message-group ai-message">
            <div class="avatar ai-avatar">
              <span class="avatar-icon">üå≤</span>
            </div>
            <div class="message-content ai-content">
              <div class="message-bubble ai-bubble">
                <p>I'll analyze the differences between customers based on the shopping dataset.</p>
                
                <!-- Main AI response content -->
                <div class="ai-response-content">
                  <!-- Áõ¥Êé•ÊòæÁ§∫ÂÜÖÂÆπÂùó -->
                  <div v-for="(block, index) in displayedBlocks" :key="index" class="content-block">
                    <!-- Normal text content -->
                    <div v-if="block.type === 'text'" class="text-block">
                      <FileLinkDetector 
                        :content="block.content" 
                        @file-preview="selectFile" 
                      />
                    </div>
                    
                    <!-- Code block (clickable) -->
                    <div v-else-if="block.type === 'code'" 
                         class="code-block-preview"
                         @click="selectCodeBlock(block)">
                      <div class="code-block-icon">üíª</div>
                      <div class="code-block-info">
                        <div class="code-block-title">{{ block.isJson ? 'JSON Data' : 'Code Block' }}</div>
                        <div class="code-block-preview-text">{{ getCodePreview(block.content) }}</div>
                      </div>
                    </div>
                    
                    <!-- ÁîüÊàêËøáÁ®ãÂùóÔºàÂèØÊäòÂè†Ôºâ -->
                    <StatusTimeline
                      v-else-if="block.type === 'status-group'"
                      :items="block.items"
                      :initialExpanded="block.expanded"
                    />
                    
                    <!-- ‰ªªÂä°Â§ÑÁêÜÂùóÔºàÂç°ÁâáÂºèÊòæÁ§∫Ôºâ -->
                    <TaskCard
                      v-else-if="block.type === 'task-block'"
                      :content="block.content"
                      :taskId="block.id"
                    />
                    
                    <!-- Êñá‰ª∂È¢ÑËßàÁªÑ‰ª∂ -->
                    <FilePreview
                      v-else-if="block.type === 'file'"
                      :fileName="block.fileName"
                      :filePath="block.filePath"
                      :fileType="block.fileType"
                      @preview="selectFile"
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      
      <!-- Code block detail panel -->
      <div class="detail-panel" v-if="selectedCodeBlock">
        <CodeBlock 
          :content="selectedCodeBlock.content"
          :isJson="selectedCodeBlock.isJson"
        />
        <button class="close-btn" @click="closeCodeDetail">√ó</button>
      </div>
      
      <!-- File preview panel -->
      <div class="detail-panel" v-if="selectedFile">
        <MarkdownPreview 
          v-if="selectedFile.fileType === 'markdown' || selectedFile.fileType === 'report'"
          :filePath="selectedFile.filePath"
          :fileName="selectedFile.fileName"
        />
        <iframe 
          v-else-if="selectedFile.fileType === 'html'"
          :src="selectedFile.filePath"
          class="html-preview"
          frameborder="0"
        ></iframe>
        <img 
          v-else-if="selectedFile.fileType === 'image'"
          :src="selectedFile.filePath"
          class="image-preview"
          alt="File preview"
        />
        <button class="close-btn" @click="closeFilePreview">√ó</button>
      </div>
      
      <!-- Dataset Preview Modal -->
      <DatasetPreview 
        v-if="isDatasetPreviewVisible" 
        :isLoading="isLoading"
        :datasetError="datasetError"
        :datasetHeaders="datasetHeaders"
        :datasetRows="datasetRows"
        @close="toggleDatasetPreview"
      />
      
      <!-- Fullscreen image preview -->
      <div v-if="fullScreenImage" class="fullscreen-overlay" @click="closeFullScreenImage">
        <div class="fullscreen-image-container">
          <img :src="fullScreenImage" alt="Fullscreen preview" class="fullscreen-image">
          <button class="close-button" @click.stop="closeFullScreenImage">√ó</button>
        </div>
      </div>
      
      <!-- ÂõûÂà∞È°∂ÈÉ®ÊåâÈíÆ -->
      <div class="back-to-top" v-show="showBackToTop" @click="scrollToTop">
        <div class="back-to-top-icon">‚Üë</div>
      </div>

      <!-- ÊòæÁ§∫ÊâÄÊúâÁîüÊàêÊñá‰ª∂ÊåâÈíÆ -->
      <div class="show-files-button" @click="scanIterationsDirectory">
        <div class="show-files-icon">üìÅ</div>
        <div class="tooltip">Êâ´ÊèèÊñá‰ª∂</div>
      </div>
      
      <!-- Ë∑≥ËøáÂä®ÁîªÊåâÈíÆ -->
      <div class="skip-animation-button" v-if="!isTypingComplete" @click="skipTypingAnimation">
        <div class="skip-animation-icon">‚è≠Ô∏è</div>
        <div class="tooltip">Ë∑≥ËøáÂä®Áîª</div>
      </div>
    </div>
  </template>
  
  <script>
  import { ref, onMounted, onUnmounted } from 'vue'
  import DatasetPreview from '@/components/chat/DatasetPreview.vue'
  import CodeBlock from '@/components/CodeBlock.vue'
  import StatusTimeline from '@/components/StatusTimeline.vue'
  import TaskCard from '@/components/TaskCard.vue'
  import MarkdownPreview from '@/components/MarkdownPreview.vue'
  import FilePreview from '@/components/FilePreview.vue'
  import FileLinkDetector from '@/components/FileLinkDetector.vue'
  
  export default {
    name: 'ChatPage',
    components: {
      DatasetPreview,
      CodeBlock,
      StatusTimeline,
      TaskCard,
      MarkdownPreview,
      FilePreview,
      FileLinkDetector
    },
    setup() {
      const query = ref("Analyze the difference between the customers")
      const isDatasetPreviewVisible = ref(false)
      const datasetHeaders = ref([])
      const datasetRows = ref([])
      const isLoading = ref(false)
      const datasetError = ref(null)
      const fullScreenImage = ref(null)
      const introContent = ref('')
      const contentBlocks = ref([]) // ÂÖ®ÈÉ®ÂÜÖÂÆπÂùó
      const displayedBlocks = ref([]) // ÂÆûÈôÖÊòæÁ§∫ÁöÑÂÜÖÂÆπÂùó
      const isTypingComplete = ref(false) // ÊòØÂê¶Â∑≤ÂÆåÊàêÊâìÂ≠ó
      const typingSpeed = ref(20) // ÊâìÂ≠óÈÄüÂ∫¶ÔºàÊØ´Áßí/Ë°åÔºâÔºå‰ªé100Êîπ‰∏∫20‰ΩøÂÖ∂Êõ¥Âø´
      const selectedCodeBlock = ref(null)
      const showBackToTop = ref(false)
      const selectedFile = ref(null)
      const generatedFiles = ref([])
      
      // ÂáÜÂ§áÊòæÁ§∫ÁöÑÂÜÖÂÆπÂçïÂÖÉÔºàÊõ¥ÁªÜÁ≤íÂ∫¶ÁöÑÊòæÁ§∫Âçï‰ΩçÔºâ
      const contentUnits = ref([]);
      const displayedUnits = ref([]);
      const currentUnitIndex = ref(0);
      // ‰øùÂ≠òÂÆöÊó∂Âô®ÂºïÁî®
      let typingIntervalRef = null;
      
      const selectCodeBlock = (block) => {
        selectedCodeBlock.value = block
      }
      
      const closeCodeDetail = () => {
        selectedCodeBlock.value = null
      }
      
      const getCodePreview = (code) => {
        if (code.length <= 80) {
          return code
        }
        return code.substring(0, 80) + '...'
      }
      
      const formatTextWithHighlights = (text) => {
        // Ê£ÄÊü•ÊñáÊú¨‰∏≠ÊòØÂê¶ÂåÖÂê´Ê†áÁ≠æ
        const containsHTMLTags = /<span|<div|<p/i.test(text);
        if (containsHTMLTags) {
          console.log("ÊñáÊú¨Â∑≤ÂåÖÂê´HTMLÊ†áÁ≠æÔºåÁõ¥Êé•ËøîÂõû");
          return text; // Â¶ÇÊûúÂ∑≤ÂåÖÂê´HTMLÊ†áÁ≠æÔºåÁõ¥Êé•ËøîÂõûÔºåÈÅøÂÖçÁ†¥ÂùèÂ∑≤ÊúâÊ†áÁ≠æ
        }
        
        // È´ò‰∫ÆÂÖ≥ÈîÆËØçÂíåÁü≠ËØ≠
        const keywords = [
          'Finding', 'available action', 'Trying action',
          'MCTS search started', 'MCTS search completed', 
          'Iteration', 'Simulation', 'state', 'depth',
          'Selected action', 'Executing action', 'Found'
        ];
        
        // ËΩ¨‰πâHTML
        let result = text.replace(/</g, '&lt;').replace(/>/g, '&gt;');
        
        // ÁâπÊÆäÂä®‰ΩúÊ†áÁ≠æ - ‰∏∫ÊØè‰∏™Âä®‰ΩúÊåáÂÆö‰∏çÂêåÁöÑÁ±ªÂêçÂíåÂåπÈÖçÊ®°Âºè
        const actionKeywords = [
          {action: 'Query2Chapters', className: 'action-query-chapters'},
          {action: 'ReviseVis', className: 'action-revise'}, 
          {action: 'Charts2Captions', className: 'action-charts-captions'}, 
          {action: 'Captions2Summaries', className: 'action-captions-summaries'}, 
          {action: 'Chapter2Tasks', className: 'action-chapter-tasks'}, 
          {action: 'Tasks2Charts', className: 'action-tasks-charts'}
        ];
        
        // ‰ΩøÁî®ÊúÄÁõ¥Êé•ÁöÑÂ≠óÁ¨¶‰∏≤ÂàÜÂâ≤ÂíåËøûÊé•ÊñπÊ≥ïÂ§ÑÁêÜÊØè‰∏™Âä®‰ΩúÂêçÁß∞
        for (const item of actionKeywords) {
          // ËøõË°åÂàÜÂâ≤Â§ÑÁêÜÔºåÂ§ÑÁêÜ‰∏Ä‰∏™Âä®‰ΩúÂêçÁß∞Âá∫Áé∞Â§öÊ¨°ÁöÑÊÉÖÂÜµ
          let parts = result.split(item.action);
          if (parts.length > 1) {
            // ËØ¥ÊòéÊâæÂà∞‰∫ÜËá≥Â∞ë‰∏Ä‰∏™ÂåπÈÖç
            console.log(`ÊõøÊç¢Âä®‰Ωú ${item.action}, Âá∫Áé∞ ${parts.length - 1} Ê¨°`);
            result = parts.join(`<span class="action-tag ${item.className}">${item.action}</span>`);
          }
        }
        
        // È´ò‰∫ÆÂÖ≥ÈîÆËØç
        keywords.forEach(keyword => {
          const regex = new RegExp(`(${keyword})`, 'gi');
          result = result.replace(regex, '<span class="highlight">$1</span>');
        });
        
        return result;
      };
      
      const openFullScreenImage = (imageSrc) => {
        fullScreenImage.value = imageSrc
      }
  
      const closeFullScreenImage = () => {
        fullScreenImage.value = null
      }
      
      const toggleDatasetPreview = async () => {
        isDatasetPreviewVisible.value = !isDatasetPreviewVisible.value
        
        // Load dataset on first open
        if (isDatasetPreviewVisible.value && datasetRows.value.length === 0) {
          await loadDataset()
        }
      }
      
      const loadDataset = async () => {
        isLoading.value = true
        datasetError.value = null
        try {
          const response = await fetch('/storyteller/dataset/shopping.csv')
          if (!response.ok) {
            throw new Error(`Failed to load dataset (${response.status})`)
          }
          
          const csvText = await response.text()
          const lines = csvText.split('\n').filter(line => line.trim() !== '')
          
          if (lines.length > 0) {
            datasetHeaders.value = lines[0].split(',').map(h => h.trim())
            
            // Only load up to 100 rows for performance
            const maxRows = Math.min(lines.length - 1, 100)
            datasetRows.value = lines.slice(1, maxRows + 1).map(line => 
              line.split(',').map(cell => cell.trim())
            )
          }
        } catch (error) {
          console.error('Error loading dataset:', error)
          datasetError.value = error.message
        } finally {
          isLoading.value = false
        }
      }
  
      onMounted(async () => {
        try {
          // Ê∑ªÂä†ÊªöÂä®ÁõëÂê¨
          window.addEventListener('scroll', handleScroll)
          
          // Read log.txt file
          const response = await fetch('/storyteller/output/log.txt')
          const logContent = await response.text()
          
          // ÊµãËØï: Âú®ÂÜÖÂÆπ‰∏≠Ê∑ªÂä†Â∏¶Ê†∑ÂºèÁöÑÁä∂ÊÄÅ‰ø°ÊÅØ
          const styledContent = logContent
            .replace(
              /ÂΩìÂâçÁä∂ÊÄÅ: ([A-Za-z0-9_.]+)/g, 
              '<div class="state-wrapper"><span class="state-tag">ÂΩìÂâçÁä∂ÊÄÅ: $1</span></div>'
            );
          
          // Áõ¥Êé•ÊèêÂèñÂπ∂Ëß£ÊûêÊï¥‰∏™Êó•ÂøóÂÜÖÂÆπ
          introContent.value = styledContent
          parseContentBlocks(styledContent)
          
          // Ê£ÄÊü•ÊòØÂê¶Â≠òÂú®‰ª£Á†ÅÂùóÔºåÂ¶ÇÊûúÊúâÂàôÂú®ÊéßÂà∂Âè∞ËæìÂá∫
          if (contentBlocks.value.some(block => block.type === 'code')) {
            console.log('ÂèëÁé∞‰ª£Á†ÅÂùó:', contentBlocks.value.filter(block => block.type === 'code').length)
          } else {
            console.log('Êú™ÊâæÂà∞‰ª£Á†ÅÂùó')
          }
          
          // Á´ãÂç≥ÊâßË°åÊñá‰ª∂Êâ´Êèè
          console.log("È°µÈù¢Âä†ËΩΩÂÆåÊàêÔºåËá™Âä®ÊâßË°åÊñá‰ª∂Êâ´Êèè");
          setTimeout(() => {
            scanIterationsDirectory();
          }, 1000); // Âª∂Ëøü1ÁßíÊâßË°åÔºåÁ°Æ‰øùÂÖ∂‰ªñÂÜÖÂÆπÂ∑≤Âä†ËΩΩÂÆåÊàê
          
        } catch (error) {
          console.error('Failed to load log file:', error)
        }
      })
  
      onUnmounted(() => {
        // ÁßªÈô§ÊªöÂä®ÁõëÂê¨
        window.removeEventListener('scroll', handleScroll);
        
        // Ê∏ÖÈô§ÊâìÂ≠óÊú∫ÊïàÊûúÁöÑÂÆöÊó∂Âô®
        if (typingIntervalRef) {
          clearInterval(typingIntervalRef);
        }
      })
  
      // Â§ÑÁêÜÊªöÂä®‰∫ã‰ª∂ÔºåÊòæÁ§∫/ÈöêËóèÂõûÂà∞È°∂ÈÉ®ÊåâÈíÆ
      const handleScroll = () => {
        // ÂΩìÈ°µÈù¢ÊªöÂä®Ë∂ÖËøá300pxÊó∂ÊòæÁ§∫ÂõûÂà∞È°∂ÈÉ®ÊåâÈíÆ
        showBackToTop.value = window.scrollY > 300
      }
  
      // Â∞ÜËß£ÊûêÂ•ΩÁöÑÂÜÖÂÆπÂùóÈÄêÊ≠•ÊòæÁ§∫Âá∫Êù•
      const startTypingAnimation = () => {
        // Â¶ÇÊûúËøòÊ≤°ÂáÜÂ§áÂÜÖÂÆπÂçïÂÖÉÔºåÂÖàÂáÜÂ§á
        if (contentUnits.value.length === 0) {
          prepareContentUnits();
        }
        
        // Â¶ÇÊûúÂä®ÁîªÂ∑≤ÂÆåÊàêÊàñÊ≤°ÊúâÂÜÖÂÆπÔºåÁõ¥Êé•ËøîÂõû
        if (isTypingComplete.value || contentUnits.value.length === 0) {
          return null;
        }
        
        // Êô∫ËÉΩÊªöÂä®ÊéßÂà∂
        let shouldAutoScroll = true; // ÈªòËÆ§Ëá™Âä®ÊªöÂä®
        
        // Ê∑ªÂä†ÊªöÂä®‰∫ã‰ª∂Ê£ÄÊµãÁî®Êà∑ÊòØÂê¶Âú®Â∫ïÈÉ®
        const scrollHandler = () => {
          // Ê£ÄÊü•Áî®Êà∑ÊòØÂê¶ÊªöÂä®Âà∞Êé•ËøëÂ∫ïÈÉ®
          const scrollPosition = window.scrollY + window.innerHeight;
          const nearBottom = document.body.scrollHeight - scrollPosition < 100;
        
          // Êõ¥Êñ∞Ëá™Âä®ÊªöÂä®Áä∂ÊÄÅ
          shouldAutoScroll = nearBottom;
          
          // Â¶ÇÊûúÂ∑≤ÁªèÂú®Â∫ïÈÉ®ÔºåÊ∑ªÂä†Ê†áÂøó"ÂÖ≥Ê≥®Â∫ïÈÉ®"
          if (nearBottom) {
            console.log("Áî®Êà∑Âú®Â∫ïÈÉ®ÔºåÂêØÁî®Ëá™Âä®ÊªöÂä®");
          } else {
            console.log("Áî®Êà∑‰∏çÂú®Â∫ïÈÉ®ÔºåÊöÇÂÅúËá™Âä®ÊªöÂä®");
          }
        };
        
        // Ê∑ªÂä†ÊªöÂä®ÁõëÂê¨
        window.addEventListener('scroll', scrollHandler);
        
        // ÂàùÂßãÊ£ÄÊü•‰∏ÄÊ¨°
        scrollHandler();
        
        // ‰ªéÂΩìÂâç‰ΩçÁΩÆÁªßÁª≠ÊòæÁ§∫
        const typingInterval = setInterval(() => {
          if (currentUnitIndex.value < contentUnits.value.length) {
            // Ê∑ªÂä†‰∏ã‰∏Ä‰∏™ÂÜÖÂÆπÂçïÂÖÉ
            processNextContentUnit();
            
            // Êõ¥Êñ∞Á¥¢Âºï
            currentUnitIndex.value++;
            
            // Ê†πÊçÆÁî®Êà∑‰ΩçÁΩÆÂÜ≥ÂÆöÊòØÂê¶Ëá™Âä®ÊªöÂä®
            if (shouldAutoScroll) {
              setTimeout(() => {
                window.scrollTo({
                  top: document.body.scrollHeight,
                  behavior: 'smooth'
                });
              }, 50);
            }
          } else {
            // ÂÖ®ÈÉ®ÊòæÁ§∫ÂÆåÊØïÔºåÊ∏ÖÈô§ÂÆöÊó∂Âô®Âíå‰∫ã‰ª∂ÁõëÂê¨
            clearInterval(typingInterval);
            window.removeEventListener('scroll', scrollHandler);
            isTypingComplete.value = true;
            typingIntervalRef = null;
            
            // Âä®ÁîªÂÆåÊàêÂêéÊâ´ÊèèiterationsÁõÆÂΩï‰∏ãÁöÑÊñá‰ª∂
            setTimeout(() => {
              scanIterationsDirectory();
            }, 500);
          }
        }, typingSpeed.value);
        
        // ‰øùÂ≠òÂÆöÊó∂Âô®ÂºïÁî®Ôºå‰ª•‰æøÂèØ‰ª•Âú®ÁªÑ‰ª∂Âç∏ËΩΩÊó∂Ê∏ÖÈô§
        typingIntervalRef = typingInterval;
        return typingInterval;
      }
  
      // Â§ÑÁêÜ‰∏ã‰∏Ä‰∏™ÂÜÖÂÆπÂçïÂÖÉ
      const processNextContentUnit = () => {
        const unit = contentUnits.value[currentUnitIndex.value];
        
        // Ê†πÊçÆÂçïÂÖÉÁ±ªÂûãËøõË°å‰∏çÂêåÂ§ÑÁêÜ
        if (unit.type === 'line') {
          // ÊôÆÈÄöÊñáÊú¨Ë°å
          displayedUnits.value.push(unit);
          
          // Âü∫‰∫éÂΩìÂâçÂÜÖÂÆπÂçïÂÖÉÊûÑÂª∫Ë¶ÅÊòæÁ§∫ÁöÑÂÜÖÂÆπÂùó
          rebuildDisplayedBlocks();
        } else if (unit.type === 'block-start') {
          // ÂùóÂºÄÂßãÊ†áËÆ∞ÔºåÂàõÂª∫‰∏Ä‰∏™Êñ∞Âùó
          displayedUnits.value.push(unit);
        } else if (unit.type === 'block-line') {
          // ÂùóÂÜÖÁöÑË°å
          displayedUnits.value.push(unit);
          
          // Êõ¥Êñ∞ÂØπÂ∫îÂùóÁöÑÂÜÖÂÆπ
          rebuildDisplayedBlocks();
        } else if (unit.type === 'block-end') {
          // ÂùóÁªìÊùüÊ†áËÆ∞
          displayedUnits.value.push(unit);
          rebuildDisplayedBlocks();
          
          // Âú®ÂùóÁªìÊùüÊó∂ÔºåÂ¶ÇÊûúÊòØ‰ª£Á†ÅÂùóÔºåËá™Âä®Âú®Âè≥‰æßÂ±ïÁ§∫
          const lastBlock = displayedBlocks.value[displayedBlocks.value.length - 1];
          if (lastBlock && lastBlock.type === 'code') {
            // Ëá™Âä®Â±ïÁ§∫‰ª£Á†ÅÂùóÂú®Âè≥‰æßÈù¢Êùø
            selectCodeBlock(lastBlock);
          }
        } else if (unit.type === 'whole-block') {
          // Êï¥ÂùóÊòæÁ§∫ÁöÑÂÜÖÂÆπ
          displayedUnits.value.push(unit);
          rebuildDisplayedBlocks();
          
          // Â¶ÇÊûúÊòØÊï¥ÂùóÊòæÁ§∫ÁöÑ‰ª£Á†ÅÂùóÔºå‰πüÂú®Âè≥‰æßÂ±ïÁ§∫
          const lastBlock = displayedBlocks.value[displayedBlocks.value.length - 1];
          if (lastBlock && lastBlock.type === 'code') {
            // Ëá™Âä®Â±ïÁ§∫‰ª£Á†ÅÂùóÂú®Âè≥‰æßÈù¢Êùø
            selectCodeBlock(lastBlock);
          }
        }
      };
  
      // Âü∫‰∫éÂ∑≤ÊòæÁ§∫ÁöÑÂÜÖÂÆπÂçïÂÖÉÈáçÂª∫ÊòæÁ§∫Âùó
      const rebuildDisplayedBlocks = () => {
        // ÂàõÂª∫‰∏Ä‰∏™Êñ∞ÁöÑÂùóÊï∞ÁªÑ
        const blocks = [];
        
        // ÂΩìÂâçÊ≠£Âú®ÊûÑÂª∫ÁöÑÂùó
        let currentBlock = null;
        let currentBlockLines = [];
        
        // ÈÅçÂéÜÊâÄÊúâÂ∑≤ÊòæÁ§∫ÁöÑÂçïÂÖÉ
        displayedUnits.value.forEach(unit => {
          if (unit.type === 'line') {
            // ÊôÆÈÄöÊñáÊú¨Ë°åÔºå‰Ωú‰∏∫ÊñáÊú¨ÂùóÊ∑ªÂä†
            blocks.push({
              type: 'text',
              content: unit.content
            });
          } else if (unit.type === 'block-start') {
            // ÂùóÂºÄÂßãÔºåËÆ∞ÂΩïÂΩìÂâçÂùóÁ±ªÂûãÂíåÂ±ûÊÄß
            currentBlock = unit.block;
            currentBlockLines = [];
          } else if (unit.type === 'block-line') {
            // ÂùóÂÜÖÂÆπË°åÔºåÊ∑ªÂä†Âà∞ÂΩìÂâçÂùó
            currentBlockLines.push(unit.content);
          } else if (unit.type === 'block-end') {
            // ÂùóÁªìÊùüÔºåÂ∞ÜÂΩìÂâçÂùóÊ∑ªÂä†Âà∞ÂùóÊï∞ÁªÑ
            if (currentBlock) {
              const block = { ...currentBlock };
              block.content = currentBlockLines.join('\n');
              blocks.push(block);
              currentBlock = null;
              currentBlockLines = [];
            }
          } else if (unit.type === 'whole-block') {
            // Êï¥ÂùóÊòæÁ§∫ÁöÑÂÜÖÂÆπ
            blocks.push(unit.block);
          }
        });
        
        // Êõ¥Êñ∞ÊòæÁ§∫ÁöÑÂùó
        displayedBlocks.value = blocks;
      };
        
      // Ë∑≥ËøáÊâìÂ≠óÊïàÊûúÔºåÁõ¥Êé•ÊòæÁ§∫ÊâÄÊúâÂÜÖÂÆπ
      const skipTypingAnimation = () => {
        if (typingIntervalRef) {
          clearInterval(typingIntervalRef);
        }
        
        // Áõ¥Êé•ÊòæÁ§∫ÊâÄÊúâÂÜÖÂÆπÂùó
        displayedBlocks.value = [...contentBlocks.value];
        isTypingComplete.value = true;
        currentUnitIndex.value = contentUnits.value.length;
        
        // Á´ãÂç≥ÊâßË°åÊñá‰ª∂Êâ´Êèè
        console.log("Ë∑≥ËøáÂä®ÁîªÔºåÁ´ãÂç≥ÊâßË°åÊñá‰ª∂Êâ´Êèè");
        scanIterationsDirectory();
        
        // ÂÖ≥Èó≠ÂèØËÉΩÊòæÁ§∫ÁöÑ‰ª£Á†ÅÂùóÔºå‰ª•‰æøÂêéÁª≠ÊòæÁ§∫report.md
        if (selectedCodeBlock.value) {
          setTimeout(() => {
            closeCodeDetail();
          }, 500);
        }
      };
  
      // Â∞ÜÂ§ßÂùóÂÜÖÂÆπÊãÜÂàÜÊàêÊõ¥Â∞èÁöÑÊòæÁ§∫ÂçïÂÖÉ
      const prepareContentUnits = () => {
        contentUnits.value = [];
        
        // ÈÅçÂéÜÊâÄÊúâÂÜÖÂÆπÂùó
        contentBlocks.value.forEach(block => {
          if (block.type === 'text') {
            // ÊñáÊú¨ÂùóÊåâË°åÊãÜÂàÜ
            const lines = block.content.split('\n');
            lines.forEach(line => {
              if (line.trim()) {
                contentUnits.value.push({
                  type: 'line',
                  content: line,
                  parentType: 'text'
                });
              }
            });
          } else if (block.type === 'task-block' || block.type === 'code') {
            // ÁâπÊÆäÂùó‰πüÊåâË°åÊãÜÂàÜ
            const lines = block.content.split('\n');
            
            // ÂÖàÊ∑ªÂä†‰∏Ä‰∏™ÂºÄÂßãÊ†áËÆ∞
            contentUnits.value.push({
              type: 'block-start',
              blockType: block.type,
              block: block
            });
            
            // ÈÄêË°åÊ∑ªÂä†ÂÜÖÂÆπ
            lines.forEach(line => {
              contentUnits.value.push({
                type: 'block-line',
                content: line,
                parentType: block.type
              });
            });
            
            // Ê∑ªÂä†ÁªìÊùüÊ†áËÆ∞
            contentUnits.value.push({
              type: 'block-end',
              blockType: block.type
            });
          } else {
            // ÂÖ∂‰ªñÁ±ªÂûãÁöÑÂùó‰Ωú‰∏∫Êï¥‰ΩìÊ∑ªÂä†
            contentUnits.value.push({
              type: 'whole-block',
              block: block
            });
            }
        });
      };
  
      // Âú®parseContentBlocksÂáΩÊï∞ÁöÑÊú´Â∞æË∞ÉÁî®ÊâìÂ≠óÊú∫ÊïàÊûú
      const parseContentBlocks = (content) => {
        console.log("ÂºÄÂßãËß£ÊûêÂÜÖÂÆπÂùóÔºåÂÜÖÂÆπÈïøÂ∫¶:", content.length);
        
        // Ê∏ÖÁ©∫‰πãÂâçÁöÑÊñá‰ª∂ÂàóË°®
        generatedFiles.value = [];
        
        // ÂæÖËß£ÊûêÂ§ÑÁêÜÁöÑÂÜÖÂÆπ
        let filteredContent = content;
        
        // Ë∑üË∏™Â∑≤ËØÜÂà´ÁöÑÁâπÊÆäÂå∫Âüü
        const markers = [];
        
        // Áî®‰∫éËÆ∞ÂΩï‰ªªÂä°ÂùóË¶ÜÁõñÁöÑ‰ΩçÁΩÆ
        const coveredPositions = {};
        
        // ËØÜÂà´‰ªªÂä°ÂùóËæπÁïå
        const taskBlockRegex = /==== Start of Task Block: ([a-zA-Z0-9-]+) ====([\s\S]*?)==== End of Task Block: \1 ====/g;
        let taskMatch;
        
        while ((taskMatch = taskBlockRegex.exec(filteredContent)) !== null) {
          const taskId = taskMatch[1];
          const taskContent = taskMatch[2].trim();
          const startIndex = taskMatch.index;
          const endIndex = taskMatch.index + taskMatch[0].length;
          
          // ËÆ∞ÂΩïËøô‰∏™‰ªªÂä°ÂùóÁöÑËµ∑Ê≠¢‰ΩçÁΩÆ
          markers.push({
            type: 'task-block',
            id: taskId,
            content: taskContent,
            start: startIndex,
            end: endIndex
          });
          
          // Ê†áËÆ∞‰ªªÂä°ÂùóË¶ÜÁõñÁöÑÊâÄÊúâ‰ΩçÁΩÆ
          for (let i = startIndex; i < endIndex; i++) {
            coveredPositions[i] = true;
          }
        }
        
        // ËØÜÂà´‰ª£Á†ÅÂùó
        const codeBlockRegex = /```(?:(?:(json))|(?:[a-zA-Z]*))\n([\s\S]*?)```/g;
        let codeMatch;
        
        while ((codeMatch = codeBlockRegex.exec(filteredContent)) !== null) {
          const isJson = !!codeMatch[1]; // Ê£ÄÊü•ÊòØÂê¶ÊòØjson‰ª£Á†ÅÂùó
          const codeContent = codeMatch[2];
          const startIndex = codeMatch.index;
          const endIndex = codeMatch.index + codeMatch[0].length;
          
          // ËÆ∞ÂΩï‰ª£Á†ÅÂùó‰ΩçÁΩÆ
            markers.push({
            type: 'code',
            content: codeContent,
            isJson,
            start: startIndex,
            end: endIndex
          });
        }
        
        // ËØÜÂà´Áä∂ÊÄÅÁªÑ
        const statusListRegex = /(- \[.*?\] .*(?:\n {2,}[^\n]+)*(?:\n(?:- \[.*?\] .*(?:\n {2,}[^\n]+)*))+)/g;
        let statusMatch;
        
        while ((statusMatch = statusListRegex.exec(filteredContent)) !== null) {
          const statusContent = statusMatch[0];
          const startIndex = statusMatch.index;
          const endIndex = statusMatch.index + statusMatch[0].length;
          
          // Ëß£ÊûêÁä∂ÊÄÅÂàóË°®È°π
          const statusItemRegex = /- \[(.*?)\] (.*?)(?:\n|$)(?:\n {2,}([^\n]+))?/g;
          const statusItems = [];
          let statusItemMatch;
          
          // ÈáçÁΩÆlastIndex‰ª•‰æø‰ªéstatusContentÁöÑÂºÄÂ§¥ÂºÄÂßãÊêúÁ¥¢
          statusItemRegex.lastIndex = 0;
          
          while ((statusItemMatch = statusItemRegex.exec(statusContent)) !== null) {
            const status = statusItemMatch[1].trim() === 'x' ? 'done' : 
                          statusItemMatch[1].trim() === '~' ? 'abandoned' : 'pending';
            
            statusItems.push({
              status,
              text: statusItemMatch[2],
              detail: statusItemMatch[3] || ''
            });
          }
          
          // Â¶ÇÊûúËá≥Â∞ëÊúâ2‰∏™Áä∂ÊÄÅÈ°πÔºåÊâçÊ∑ªÂä†‰∏∫Áä∂ÊÄÅÁªÑ
          if (statusItems.length >= 2) {
            markers.push({
              type: 'status-group',
              items: statusItems,
              start: startIndex,
              end: endIndex
            });
          }
        }
        
        // ÊúÄÁªàÂÜÖÂÆπÂùóÊï∞ÁªÑ
        let blocks = [];
        
        // ÂÖàÊ∑ªÂä†ÊâÄÊúâ‰ªªÂä°Âùó
        for (const marker of markers.filter(m => m.type === 'task-block')) {
          blocks.push({
            type: 'task-block',
            id: marker.id,
            content: marker.content,
            position: marker.start
          });
        }
        
        // Ê∑ªÂä†ÊâÄÊúâÈùû‰ªªÂä°ÂùóÊ†áËÆ∞
        for (const marker of markers.filter(m => m.type !== 'task-block')) {
          // Ê£ÄÊü•ËØ•Ê†áËÆ∞ÊòØÂê¶Âú®‰ªªÂä°ÂùóË¶ÜÁõñÁöÑÂå∫ÂüüÂÜÖ
          let isInsideTaskBlock = false;
          // Ê£ÄÊü•Ê†áËÆ∞ÁöÑ‰∏≠ÂøÉÁÇπÊòØÂê¶Âú®‰ªªÂä°ÂùóÂÜÖÔºåÊàñËÄÖÂ§ßÈÉ®ÂàÜÂÜÖÂÆπÊòØÂê¶Âú®‰ªªÂä°ÂùóÂÜÖ
          const markerLength = marker.end - marker.start;
          let coveredCount = 0;
          
          for (let i = marker.start; i < marker.end; i++) {
            if (coveredPositions[i]) {
              coveredCount++;
            }
          }
          
          // Â¶ÇÊûúË∂ÖËøá70%ÁöÑÂÜÖÂÆπÂú®‰ªªÂä°ÂùóÂÜÖÔºåÂàôËÆ§‰∏∫ÂÆÉÊòØ‰ªªÂä°ÂùóÁöÑ‰∏ÄÈÉ®ÂàÜ
          if (coveredCount / markerLength > 0.7) {
            isInsideTaskBlock = true;
            // ËæìÂá∫Êó•ÂøóÔºåËÆ∞ÂΩïËøáÊª§ÊéâÁöÑ‰ª£Á†ÅÂùóÂÜÖÂÆπ
            if (marker.type === 'code') {
              console.log('ËøáÊª§Êéâ‰ªªÂä°ÂùóÂÜÖÁöÑ‰ª£Á†ÅÂùó:', marker.content.substring(0, 50) + (marker.content.length > 50 ? '...' : ''));
            }
          }
          
          // Â¶ÇÊûú‰∏çÂú®‰ªªÂä°ÂùóÂÜÖÔºåÊâçÊ∑ªÂä†Âà∞blocks‰∏≠
          if (!isInsideTaskBlock) {
          if (marker.type === 'code') {
            blocks.push({
              type: 'code',
              content: marker.content,
              isJson: marker.isJson,
              position: marker.start
            });
          } else if (marker.type === 'status-group') {
            blocks.push({
              type: 'status-group',
              items: marker.items,
              expanded: false,
              position: marker.start
            });
            }
          }
        }
        
        // Â§ÑÁêÜÊñáÊú¨Èó¥ÈöôÔºöÂ∞ÜÊú™Ë¢´‰ªª‰ΩïÊ†áËÆ∞Ë¶ÜÁõñÁöÑÂÜÖÂÆπ‰Ωú‰∏∫ÊñáÊú¨ÂùóÊ∑ªÂä†
        let textStart = 0;
        let inText = false;
        
        for (let i = 0; i < filteredContent.length; i++) {
          // Ê£ÄÊü•ÂΩìÂâç‰ΩçÁΩÆÊòØÂê¶Ë¢´‰ªª‰ΩïÊ†áËÆ∞Ë¶ÜÁõñ
          let isMarked = false;
          for (const marker of markers) {
            if (i >= marker.start && i < marker.end) {
              isMarked = true;
              break;
            }
          }
          
          // Â¶ÇÊûúÊ≤°ÊúâË¢´Ê†áËÆ∞Ôºå‰∏î‰∏çÊòØ‰ªªÂä°ÂùóÂå∫ÂüüÔºåÂàô‰∏∫ÊôÆÈÄöÊñáÊú¨
          if (!isMarked && !coveredPositions[i]) {
            if (!inText) {
              // ÂºÄÂßã‰∏Ä‰∏™Êñ∞ÁöÑÊñáÊú¨Âùó
              textStart = i;
              inText = true;
            }
          } else if (inText) {
            // ÁªìÊùüÂΩìÂâçÊñáÊú¨Âùó
            const textContent = filteredContent.substring(textStart, i).trim();
            if (textContent) {
              blocks.push({
                type: 'text',
                content: textContent,
                position: textStart
              });
            }
            inText = false;
          }
        }
        
        // Â§ÑÁêÜÊúÄÂêé‰∏Ä‰∏™ÂèØËÉΩÁöÑÊñáÊú¨Âùó
        if (inText) {
          const textContent = filteredContent.substring(textStart).trim();
          if (textContent) {
            blocks.push({
              type: 'text',
              content: textContent,
              position: textStart
            });
          }
        }
        
        // ÊåâÁÖßÂéüÂßã‰ΩçÁΩÆÊéíÂ∫èÊâÄÊúâÂÜÖÂÆπÂùó
        blocks.sort((a, b) => {
          const posA = a.position !== undefined ? a.position : Infinity;
          const posB = b.position !== undefined ? b.position : Infinity;
          return posA - posB;
        });
        
        // ÂéªÈô§‰ΩçÁΩÆ‰ø°ÊÅØÔºà‰∏çÈúÄË¶Å‰º†ÈÄíÁªôUIÔºâ
        blocks = blocks.map(item => {
          // eslint-disable-next-line no-unused-vars
          const { position, ...rest } = item;
          return rest;
        });
        
        // Ê∑ªÂä†ÊÄªÁªìÊó•Âøó
        const blockTypeCounts = blocks.reduce((counts, block) => {
          counts[block.type] = (counts[block.type] || 0) + 1;
          return counts;
        }, {});
        
        console.log("Ëß£ÊûêÂÆåÊàêÔºåÂÜÖÂÆπÂùóÊÄªÊï∞:", blocks.length);
        console.log("ÂêÑÁ±ªÂûãÂùóÊï∞Èáè:", blockTypeCounts);
        
        contentBlocks.value = blocks;
        
        // ÂêØÂä®ÊâìÂ≠óÊú∫ÊïàÊûú
        if (typingIntervalRef) {
          clearInterval(typingIntervalRef);
        }
        typingIntervalRef = startTypingAnimation();
      }
  
      const scrollToTop = () => {
        window.scrollTo({ top: 0, behavior: 'smooth' })
      }
  
      // ÈÄâÊã©Êñá‰ª∂ËøõË°åÈ¢ÑËßà
      const selectFile = (file) => {
        console.log(`È¢ÑËßàÊñá‰ª∂: ${file.fileName}, Á±ªÂûã: ${file.fileType}, Ë∑ØÂæÑ: ${file.filePath}`);
        
        // ÂØπ‰∫éÂõæÁâáÊñá‰ª∂ÔºåÂÖàÊ£ÄÊü•ÊòØÂê¶Â≠òÂú®
        if (file.fileType === 'image') {
          checkAndPreviewImage(file);
        } else {
          selectedFile.value = file;
        }
        
        // Â¶ÇÊûúÂ∑≤ÁªèÊâìÂºÄ‰∫Ü‰ª£Á†ÅÂùóËØ¶ÊÉÖÔºåÂàôÂÖ≥Èó≠ÂÆÉ
        if (selectedCodeBlock.value) {
          selectedCodeBlock.value = null;
        }
      }
  
      // Ê£ÄÊü•ÂõæÁâáÊòØÂê¶Â≠òÂú®Âπ∂È¢ÑËßà
      const checkAndPreviewImage = (file) => {
        // ÂÖàÊ£ÄÊü•ÂéüÂßãURLÊ†ºÂºè storyteller/output/iterations/iteration_X/charts/
        // const originalUrlPattern = /storyteller\/output\/iterations\/iteration_\d+\/charts\//;
        
        // Â∞ùËØïÁöÑË∑ØÂæÑÊï∞ÁªÑ
        const pathsToTry = [
          file.filePath, // ÂéüÂßãË∑ØÂæÑ
          `/${file.filePath}`, // Ê∑ªÂä†ÂâçÂØºÊñúÊù†
          `/storyteller/output/iterations/iteration_1/charts/${file.fileName}`, // ÊúÄÂèØËÉΩÁöÑË∑ØÂæÑ
          `/storyteller/output/iterations/iteration_2/charts/${file.fileName}`,
          `/storyteller/output/iterations/iteration_3/charts/${file.fileName}`,
          `/storyteller/output/visualizations/${file.fileName}`,
          `/storyteller/output/images/${file.fileName}`,
          `/storyteller/output/charts/${file.fileName}`
        ];
        
        console.log(`Â∞ùËØïÂä†ËΩΩÂõæÁâá: ${file.fileName}ÔºåÂÖ±${pathsToTry.length}‰∏™ÂèØËÉΩË∑ØÂæÑ`);
        tryLoadImage(pathsToTry, 0, file);
      }
  
      // ÈÄíÂΩíÂ∞ùËØïÂä†ËΩΩ‰∏çÂêåË∑ØÂæÑÁöÑÂõæÁâá
      const tryLoadImage = (paths, index, file) => {
        if (index >= paths.length) {
          // ÊâÄÊúâË∑ØÂæÑÈÉΩÂ∞ùËØïËøá‰∫ÜÔºåÊòæÁ§∫ÈîôËØØ
          console.error(`Êó†Ê≥ïÂä†ËΩΩÂõæÁâáÔºåÊâÄÊúâË∑ØÂæÑÈÉΩÂ§±Ë¥•: ${file.fileName}`);
          return;
        }
        
        const img = new Image();
        img.onload = () => {
          // ÂõæÁâáÂä†ËΩΩÊàêÂäü
          console.log(`ÂõæÁâáÂú®Êõø‰ª£Ë∑ØÂæÑÂä†ËΩΩÊàêÂäü: ${paths[index]}`);
          file.filePath = paths[index]; // Êõ¥Êñ∞Êñá‰ª∂Ë∑ØÂæÑ
          selectedFile.value = file;
        };
        img.onerror = () => {
          // Â∞ùËØï‰∏ã‰∏Ä‰∏™Ë∑ØÂæÑ
          tryLoadImage(paths, index + 1, file);
        };
        img.src = paths[index];
      }
  
      // ÂÖ≥Èó≠Êñá‰ª∂È¢ÑËßà
      const closeFilePreview = () => {
        selectedFile.value = null
      }
  
      // Âú®Âä®ÁîªÂÆåÊàêÂêéÊâ´ÊèèÊòæÁ§∫iterationsÁõÆÂΩï‰∏ãÁöÑÊâÄÊúâÊñá‰ª∂
      const scanIterationsDirectory = async () => {
        console.log("ÂºÄÂßãÊâ´ÊèèÊú¨Âú∞ÁõÆÂΩï‰∏ãÁöÑÊñá‰ª∂...");
        
        // Ê∏ÖÁ©∫ÂΩìÂâçÊñá‰ª∂ÂàóË°®
        generatedFiles.value = [];
        
        // ÁªìÊûúÊï∞ÁªÑ
        const foundFiles = [];
        
        try {
          // Ë¶ÅÊâ´ÊèèÁöÑÁõÆÂΩï - Ëøô‰∫õË∑ØÂæÑÁî®‰∫éfetchËØ∑Ê±Ç - ÂáèÂ∞ëË∑ØÂæÑÊï∞ÈáèÔºåÂè™Êâ´ÊèèÈ´ò‰ºòÂÖàÁ∫ßÁõÆÂΩï
          const highPriorityPaths = [
            "/storyteller/output/iterations/iteration_1/charts",
            "/storyteller/output/iterations/iteration_1/reports",
            "/storyteller/output/iterations/iteration_2/charts",
            "/storyteller/output/iterations/iteration_3/charts"
          ];
          
          // ÂàõÂª∫‰∏Ä‰∏™Â∑•ÂÖ∑ÂáΩÊï∞ÔºåÁî®‰∫éÂ∏¶Ë∂ÖÊó∂ÁöÑËé∑ÂèñÊñá‰ª∂
          const fetchWithTimeout = async (url, options = {}, timeout = 1000) => {
            const controller = new AbortController();
            const id = setTimeout(() => controller.abort(), timeout);
            
            try {
              const response = await fetch(url, {
                ...options,
                signal: controller.signal
              });
              clearTimeout(id);
              return response;
            } catch (error) {
              clearTimeout(id);
              throw error;
            }
          };
          
          // Áõ¥Êé•Â∞ùËØïÂ∏∏ËßÅÁöÑÊä•ÂëäÊñá‰ª∂ - ÊúÄÈ´ò‰ºòÂÖàÁ∫ß
          const commonReportFiles = [
            "/storyteller/output/iterations/iteration_1/reports/report.md",
            "/storyteller/output/iterations/iteration_1/report.md",
            "/storyteller/output/iterations/iteration_1/reports/summary.md",
            "/storyteller/output/iterations/iteration_2/reports/report.md"
          ];
          
          // Âπ∂Ë°åÊ£ÄÊü•Êä•ÂëäÊñá‰ª∂
          const reportFilePromises = commonReportFiles.map(async (filePath) => {
            try {
              const response = await fetchWithTimeout(filePath, { method: 'HEAD' }, 800);
              if (response.ok) {
                console.log(`ÊâæÂà∞Êä•ÂëäÊñá‰ª∂: ${filePath}`);
                const fileName = filePath.split('/').pop();
                foundFiles.push({
                  fileName,
                  filePath,
                  fileType: 'markdown',
                  priority: 1 // È´ò‰ºòÂÖàÁ∫ß
                });
                return true;
              }
            } catch (err) {
              // ÂøΩÁï•ÈîôËØØ
            }
            return false;
          });
          
          // Á≠âÂæÖÊâÄÊúâÊä•ÂëäÊñá‰ª∂Ê£ÄÊü•ÂÆåÊàê
          await Promise.all(reportFilePromises);
          
          // Â∏∏ËßÅÁöÑÂõæË°®Êñá‰ª∂
          const commonChartFiles = [
            "/storyteller/output/iterations/iteration_1/charts/Comparison of purchase amount by gender.png",
            "/storyteller/output/iterations/iteration_1/charts/Comparison of product category preferences across different age groups.png",
            "/storyteller/output/iterations/iteration_1/charts/Analysis of correlation between purchase amount and previous purchases.png",
            "/storyteller/output/iterations/iteration_1/charts/Customer segmentation based on purchase behavior.png",
            "/storyteller/output/iterations/iteration_1/charts/AgeDistribution.png",
            "/storyteller/output/iterations/iteration_1/charts/GenderDistribution.png",
            "/storyteller/output/iterations/iteration_1/charts/PurchaseAmountDistribution.png",
            "/storyteller/output/iterations/iteration_1/charts/CategoryPreference.png",
            "/storyteller/output/iterations/iteration_1/charts/CustomerSegmentation.png"
          ];
          
          // Âπ∂Ë°åÊ£ÄÊü•Â∏∏ËßÅÂõæË°®Êñá‰ª∂
          const chartFilePromises = commonChartFiles.map(async (filePath) => {
            try {
              const response = await fetchWithTimeout(filePath, { method: 'HEAD' }, 800);
              if (response.ok) {
                console.log(`ÊâæÂà∞ÂõæË°®Êñá‰ª∂: ${filePath}`);
                const fileName = filePath.split('/').pop();
                foundFiles.push({
                  fileName,
                  filePath,
                  fileType: 'image',
                  priority: 2 // Ê¨°È´ò‰ºòÂÖàÁ∫ß
                });
                return true;
              }
            } catch (err) {
              // ÂøΩÁï•ÈîôËØØ
            }
            return false;
          });
          
          // Á≠âÂæÖÊâÄÊúâÂõæË°®Êñá‰ª∂Ê£ÄÊü•ÂÆåÊàê
          await Promise.all(chartFilePromises);
          
          // Â¶ÇÊûúÂ∑≤ÁªèÊâæÂà∞‰∫ÜË∂≥Â§üÁöÑÊñá‰ª∂ÔºàËá≥Â∞ë1‰∏™Êä•ÂëäÂíå3‰∏™ÂõæË°®ÔºâÔºåÂàôÁõ¥Êé•ËøîÂõûÁªìÊûú
          const foundReports = foundFiles.filter(f => f.fileType === 'markdown').length;
          const foundCharts = foundFiles.filter(f => f.fileType === 'image').length;
          
          if (foundReports >= 1 && foundCharts >= 3) {
            console.log("Â∑≤ÊâæÂà∞Ë∂≥Â§üÁöÑÊñá‰ª∂ÔºåË∑≥ËøáÂêéÁª≠Êâ´Êèè");
          } else {
            // Â¶ÇÊûúÊ≤°ÊúâÊâæÂà∞Ë∂≥Â§üÁöÑÊñá‰ª∂ÔºåÂàôÁªßÁª≠Êâ´ÊèèÂ∏∏ËßÅÁõÆÂΩï
            // ‰ªÖÂú®ÂâçÈù¢ÁöÑÂø´ÈÄüÊâ´ÊèèÊ≤°ÊúâÊâæÂà∞Ë∂≥Â§üÊñá‰ª∂Êó∂ÊâßË°å
            
            // Âπ∂Ë°åÊâ´ÊèèÊâÄÊúâÈ´ò‰ºòÂÖàÁ∫ßÁõÆÂΩï
            const scanPromises = highPriorityPaths.map(dirPath => {
              return scanHighPriorityDirectory(dirPath, foundFiles);
            });
            
            await Promise.all(scanPromises);
          }
        } catch (err) {
          console.error("Êâ´ÊèèËøáÁ®ã‰∏≠ÂèëÁîüÈîôËØØ:", err);
        }
        
        // Â¶ÇÊûúÊâæÂà∞‰∫ÜÊñá‰ª∂ÔºåÊ∑ªÂä†Âà∞ÁîüÊàêÊñá‰ª∂ÂàóË°®Âπ∂ÊòæÁ§∫
        if (foundFiles.length > 0) {
          console.log(`ÊÄªÂÖ±ÊâæÂà∞ ${foundFiles.length} ‰∏™Êñá‰ª∂`);
          
          // ÂéªÈô§ÈáçÂ§çÊñá‰ª∂
          const uniqueFiles = [];
          const seenPaths = new Set();
          
          for (const file of foundFiles) {
            if (!seenPaths.has(file.filePath)) {
              seenPaths.add(file.filePath);
              uniqueFiles.push(file);
            }
          }
          
          // Êåâ‰ºòÂÖàÁ∫ßÂíåÊñá‰ª∂Á±ªÂûãÊéíÂ∫è
          uniqueFiles.sort((a, b) => {
            // È¶ñÂÖàÊåâ‰ºòÂÖàÁ∫ßÊéíÂ∫è
            const priorityA = a.priority || 999;
            const priorityB = b.priority || 999;
            if (priorityA !== priorityB) {
              return priorityA - priorityB;
            }
            
            // ÂÖ∂Ê¨°ÊåâÊñá‰ª∂Á±ªÂûãÊéíÂ∫è
            const typeOrder = { 'markdown': 1, 'image': 2, 'html': 3, 'json': 4, 'csv': 5, 'text': 6, 'other': 7 };
            const typeA = typeOrder[a.fileType] || 999;
            const typeB = typeOrder[b.fileType] || 999;
            return typeA - typeB;
          });
          
          // Êõ¥Êñ∞ÁîüÊàêÊñá‰ª∂ÂàóË°®
          generatedFiles.value = uniqueFiles;
          
          // ÂàõÂª∫ÂÜÖÂÆπÂùóÊòæÁ§∫ÊâæÂà∞ÁöÑÊñá‰ª∂
          const fileDisplayBlock = {
            type: 'text',
            content: `## Êâ´ÊèèÁªìÊûú\nÂÖ±ÊâæÂà∞ ${uniqueFiles.length} ‰∏™Êñá‰ª∂:`
          };
          
          // ÁßªÈô§Áé∞ÊúâÁöÑÊâ´ÊèèÁªìÊûúÂíåÊñá‰ª∂Âùó
          contentBlocks.value = contentBlocks.value.filter(
            block => !(
              (block.type === 'text' && 
               (block.content.includes('Êâ´ÊèèÁªìÊûú') || block.content.includes('Êñá‰ª∂ ('))) || 
              block.type === 'file'
            )
          );
          
          // Ê∑ªÂä†Ê†áÈ¢òÂùó
          contentBlocks.value.push(fileDisplayBlock);
          
          // ÂàÜÁ±ªÊñá‰ª∂
          const imageFiles = uniqueFiles.filter(file => file.fileType === 'image');
          const docFiles = uniqueFiles.filter(file => ['markdown', 'html', 'text'].includes(file.fileType));
          const dataFiles = uniqueFiles.filter(file => ['json', 'csv'].includes(file.fileType));
          const otherFiles = uniqueFiles.filter(file => 
            !['image', 'markdown', 'html', 'text', 'json', 'csv'].includes(file.fileType)
          );
          
          // ÂÖàÊ∑ªÂä†ÊñáÊ°£Êñá‰ª∂Ôºà‰ºòÂÖàÁ∫ßÊúÄÈ´òÔºâ
          if (docFiles.length > 0) {
            contentBlocks.value.push({
              type: 'text',
              content: `### ÊñáÊ°£Êñá‰ª∂ (${docFiles.length}‰∏™)`
            });
            
            for (const file of docFiles) {
              contentBlocks.value.push({
                type: 'file',
                fileName: file.fileName,
                filePath: file.filePath,
                fileType: file.fileType
              });
            }
            
            // Ëá™Âä®Âú®Âè≥‰æßÊòæÁ§∫report.mdÊñá‰ª∂
            setTimeout(() => {
              // ÂØªÊâæreport.mdÊñá‰ª∂
              const reportFile = docFiles.find(file => 
                file.fileName.toLowerCase() === 'report.md' || 
                file.fileName.toLowerCase().includes('report') && file.fileType === 'markdown'
              );
              
              if (reportFile) {
                console.log('ÊâæÂà∞report.mdÊñá‰ª∂ÔºåËá™Âä®ÊòæÁ§∫Âú®Âè≥‰æßÈù¢Êùø', reportFile);
                selectFile(reportFile);
              } else {
                // Â¶ÇÊûúÊ≤°ÊúâÊâæÂà∞report.mdÔºåÂ∞ùËØïÊâæ‰ªª‰ΩïmarkdownÊñá‰ª∂
                const anyMarkdown = docFiles.find(file => file.fileType === 'markdown');
                if (anyMarkdown) {
                  console.log('Êú™ÊâæÂà∞report.mdÊñá‰ª∂ÔºåÊòæÁ§∫Á¨¨‰∏Ä‰∏™ÂèØÁî®ÁöÑMarkdownÊñá‰ª∂', anyMarkdown);
                  selectFile(anyMarkdown);
                }
              }
            }, 500); // ÂáèÂ∞ëÂª∂ËøüÊó∂Èó¥
          }
          
          // Ê∑ªÂä†ÂõæÁâáÊñá‰ª∂
          if (imageFiles.length > 0) {
            contentBlocks.value.push({
              type: 'text',
              content: `### ÂõæÁâáÊñá‰ª∂ (${imageFiles.length}‰∏™)`
            });
            
            for (const file of imageFiles) {
              contentBlocks.value.push({
                type: 'file',
                fileName: file.fileName,
                filePath: file.filePath,
                fileType: file.fileType
              });
            }
          }
          
          // Ê∑ªÂä†Êï∞ÊçÆÊñá‰ª∂
          if (dataFiles.length > 0) {
            contentBlocks.value.push({
              type: 'text',
              content: `### Êï∞ÊçÆÊñá‰ª∂ (${dataFiles.length}‰∏™)`
            });
            
            for (const file of dataFiles) {
              contentBlocks.value.push({
                type: 'file',
                fileName: file.fileName,
                filePath: file.filePath,
                fileType: file.fileType
              });
            }
          }
          
          // Ê∑ªÂä†ÂÖ∂‰ªñÊñá‰ª∂
          if (otherFiles.length > 0) {
            contentBlocks.value.push({
              type: 'text',
              content: `### ÂÖ∂‰ªñÊñá‰ª∂ (${otherFiles.length}‰∏™)`
            });
            
            for (const file of otherFiles) {
              contentBlocks.value.push({
                type: 'file',
                fileName: file.fileName,
                filePath: file.filePath,
                fileType: file.fileType
              });
            }
          }
          
          // Êõ¥Êñ∞ÊòæÁ§∫ÁöÑÂùó
          displayedBlocks.value = [...contentBlocks.value];
        } else {
          console.log("Ê≤°ÊúâÊâæÂà∞‰ªª‰ΩïÊñá‰ª∂");
          
          // Ê∑ªÂä†‰∏Ä‰∏™ÊèêÁ§∫Âùó
          const errorBlock = {
            type: 'text',
            content: '## Êâ´ÊèèÁªìÊûú\nÊú™ÊâæÂà∞‰ªª‰ΩïÊñá‰ª∂„ÄÇËØ∑Ê£ÄÊü•Êñá‰ª∂Ë∑ØÂæÑÊòØÂê¶Ê≠£Á°ÆÔºåÊàñËÄÖÂ∞ùËØïÂÖ∂‰ªñË∑ØÂæÑ„ÄÇ'
          };
          
          // ÁßªÈô§Áé∞ÊúâÁöÑÊâ´ÊèèÁªìÊûúÂíåÊñá‰ª∂Âùó
          contentBlocks.value = contentBlocks.value.filter(
            block => !(
              (block.type === 'text' && 
               (block.content.includes('Êâ´ÊèèÁªìÊûú') || block.content.includes('Êñá‰ª∂ ('))) || 
              block.type === 'file'
            )
          );
          
          contentBlocks.value.push(errorBlock);
          
          // Êõ¥Êñ∞ÊòæÁ§∫ÁöÑÂùó
          displayedBlocks.value = [...contentBlocks.value];
        }
      };
      
      // Êâ´ÊèèÈ´ò‰ºòÂÖàÁ∫ßÁõÆÂΩï - Âè™Êâ´ÊèèÊúÄÊúâÂèØËÉΩÂ≠òÂú®ÁöÑÊñá‰ª∂Á±ªÂûã
      const scanHighPriorityDirectory = async (dirPath, foundFiles) => {
        // Ê†πÊçÆÁõÆÂΩïË∑ØÂæÑÈÄâÊã©ÈÄÇÂΩìÁöÑÊñá‰ª∂Á±ªÂûã
        let fileTypes = [];
        if (dirPath.includes('charts')) {
          fileTypes = ['png', 'jpg', 'svg']; // ÂõæË°®ÁõÆÂΩï‰∏ªË¶ÅÊ£ÄÊü•ÂõæÁâáÊñá‰ª∂
        } else if (dirPath.includes('reports')) {
          fileTypes = ['md', 'html']; // Êä•ÂëäÁõÆÂΩï‰∏ªË¶ÅÊ£ÄÊü•ÊñáÊ°£Êñá‰ª∂
        } else {
          fileTypes = ['md', 'html', 'png', 'jpg']; // ÂÖ∂‰ªñÁõÆÂΩïÊ£ÄÊü•Â§öÁßçÁ±ªÂûã
        }
        
        // ÁîüÊàêË¶ÅÊ£ÄÊü•ÁöÑÊñá‰ª∂ÂêçÊï∞ÁªÑ - ÂáèÂ∞ëÂ∞ùËØïÁöÑÊñá‰ª∂Êï∞Èáè
        const filesToCheck = [];
        
        // Ê∑ªÂä†Â∏∏ËßÅÁöÑÁ¥¢ÂºïÂíåÊä•ÂëäÊñá‰ª∂
        if (fileTypes.includes('md') || fileTypes.includes('html')) {
          filesToCheck.push(...[
            'report.md', 'summary.md', 'analysis.md',
            'index.html', 'report.html'
          ].filter(f => fileTypes.includes(f.split('.').pop())));
        }
        
        // Ê∑ªÂä†Â∏∏ËßÅÁöÑÊï∞Â≠óÂ∫èÂè∑ÂõæË°®Êñá‰ª∂
        if (fileTypes.includes('png') || fileTypes.includes('jpg') || fileTypes.includes('svg')) {
          // Âè™Ê£ÄÊü•Ââç5‰∏™Êï∞Â≠óÂ∫èÂè∑
          for (let i = 1; i <= 5; i++) {
            for (const ext of fileTypes.filter(t => ['png', 'jpg', 'svg'].includes(t))) {
              filesToCheck.push(`chart_${i}.${ext}`);
              filesToCheck.push(`figure_${i}.${ext}`);
            }
          }
        }
        
        // Âπ∂Ë°åÊ£ÄÊü•ÊâÄÊúâÊñá‰ª∂
        const fetchPromises = filesToCheck.map(async (fileName) => {
          const filePath = `${dirPath}/${fileName}`;
          try {
            const response = await fetchWithTimeout(filePath, { method: 'HEAD' }, 800);
            if (response.ok) {
              console.log(`ÊâæÂà∞Êñá‰ª∂: ${filePath}`);
              // Á°ÆÂÆöÊñá‰ª∂Á±ªÂûã
              const ext = fileName.split('.').pop().toLowerCase();
              let fileType = detectFileType(ext);
              
              // Ê∑ªÂä†Âà∞ÊâæÂà∞ÁöÑÊñá‰ª∂ÂàóË°®
              foundFiles.push({
                fileName,
                filePath,
                fileType,
                priority: fileType === 'markdown' ? 1 : (fileType === 'image' ? 2 : 3)
              });
              return true;
            }
          } catch (err) {
            // ÂøΩÁï•ÈîôËØØ
          }
          return false;
        });
        
        // Á≠âÂæÖÊâÄÊúâÊ£ÄÊü•ÂÆåÊàê
        await Promise.all(fetchPromises);
      };
      
      // Âπ∂ËÆæÁΩÆ‰∏Ä‰∏™Â∏¶Ë∂ÖÊó∂ÁöÑfetchÂ∑•ÂÖ∑ÂáΩÊï∞
      const fetchWithTimeout = async (url, options = {}, timeout = 1000) => {
        const controller = new AbortController();
        const id = setTimeout(() => controller.abort(), timeout);
        
        try {
          const response = await fetch(url, {
            ...options,
            signal: controller.signal
          });
          clearTimeout(id);
          return response;
        } catch (error) {
          clearTimeout(id);
          throw error;
        }
      };
      
      // Ê†πÊçÆÊâ©Â±ïÂêçÊ£ÄÊµãÊñá‰ª∂Á±ªÂûã
      const detectFileType = (ext) => {
        if (['png', 'jpg', 'jpeg', 'gif', 'svg'].includes(ext)) {
          return 'image';
        } else if (ext === 'md') {
          return 'markdown';
        } else if (ext === 'html') {
          return 'html';
        } else if (['json', 'csv'].includes(ext)) {
          return ext;
        } else if (ext === 'txt') {
          return 'text';
        } else {
          return 'other';
        }
      };
  
      return {
        query,
        isDatasetPreviewVisible,
        datasetHeaders,
        datasetRows,
        isLoading,
        datasetError,
        fullScreenImage,
        introContent,
        contentBlocks,
        displayedBlocks,
        isTypingComplete,
        typingSpeed,
        selectedCodeBlock,
        selectCodeBlock,
        closeCodeDetail,
        getCodePreview,
        formatTextWithHighlights,
        openFullScreenImage,
        closeFullScreenImage,
        toggleDatasetPreview,
        loadDataset,
        showBackToTop,
        scrollToTop,
        skipTypingAnimation,
        selectedFile,
        selectFile,
        closeFilePreview,
        scanIterationsDirectory
      }
    }
  }
